import { Component, Event, EventEmitter, h, Host, Prop } from '@stencil/core';
import { TpTema } from './utils/themes.interface';

@Component({
  tag: 'tp-themes',
  styleUrl: 'themes.scss',
  scoped: true,
})
export class TpThemes {
  /**
   * todo
   */
  @Prop({ reflect: true }) ativo?: 'theme-gold' | 'theme-recursos' | string;

  /**
   * todo
   */
  @Prop({ reflect: true }) temas?: TpTema[];

  /**
   * todo
   */
  @Event() tpChange!: EventEmitter<string>;

  temaSelecionado(temaAtivo: any) {
    this.ativo = temaAtivo;
    this.tpChange.emit(this.ativo);
  }

  render() {
    const { temas } = this;

    return (
      <Host from-stencil>
        <ion-radio-group
          onIonChange={(ev) => this.temaSelecionado(ev.detail.value)}
          value={this.ativo}
        >
          {temas?.map((tema: TpTema) => (
            <div
              class={`tp-theme tp-theme--${tema.value}`}
              onClick={() => (this.ativo = tema.value)}
            >
              <div class="tp-theme__left">
                <div class="tp-theme__circle"></div>
              </div>
              <div class="tp-theme__right">
                <div class="tp-theme__bar"></div>
                <div class="tp-theme__bar tp-theme__bar--small"></div>
                <div class="tp-theme__wrapper">
                  <ion-radio
                    value={tema.value}
                    class="tp-theme__radio"
                  ></ion-radio>
                  <span class="tp-theme__name">{tema.label}</span>
                </div>
              </div>
            </div>
          ))}
        </ion-radio-group>
      </Host>
    );
  }
}
